import React from 'react';
import { AiFillStar, AiFillHeart, AiFillPlayCircle, AiFillPauseCircle } from 'react-icons/ai';
import { FiPlay, FiPause } from 'react-icons/fi';
import { MdAddCircle } from 'react-icons/md';
import './Song.scss';

function Song(props) {
    const { value, playLists, currentSongIndex, setCurrentSongIndex, isPlaying, setIsPlaying } = props;

    const song = {
        star: true,
        padder: true,
        playsFill: false,
        col: ''
    };

    switch (value) {
        case "song":
            song.star = true;
            song.padder = true;
            song.playsFill = false;
            song.col = "col-xl-2 col-lg-3 col-md-4 col-sm-6 col-6 items-song";
            break;
        case "new":
            song.star = false;
            song.padder = false;
            song.playsFill = true;
            song.col = "col-xl-3 col-lg-4 col-6 items-new";
            break;
        default:
    }

    function handlePlaySong(e) {
        let newCurrentIndex = Number(e.currentTarget.dataset.song);
        setCurrentSongIndex(newCurrentIndex);
        if (!isPlaying) {
            setIsPlaying(!isPlaying);
        }
    }

    return (
        <div className={`items px-lg-3 px-md-2 px-sm-2 px-3 ${song.col}`}>
            <div className={`song ${playLists.id === currentSongIndex && "open"}`} data-song={playLists.id}>
                <div className="top">
                    {value === "item" && <span className="time">03:20</span>}
                    <div className="play-container">
                        {song.star &&
                            <div className="star">
                                <AiFillStar />
                                <AiFillStar />
                                <AiFillStar />
                                <AiFillStar />
                                <AiFillStar />
                            </div>
                        }
                        <div
                            className="play"
                            data-song={playLists.id}
                            onClick={(e) => handlePlaySong(e)}
                        >
                            {song.playsFill ?
                                <button>
                                    {isPlaying && currentSongIndex === playLists.id ? <AiFillPlayCircle /> : < AiFillPauseCircle />}
                                </button>
                                :
                                <button>
                                    {isPlaying && currentSongIndex === playLists.id ? < FiPause /> : <FiPlay />}
                                </button>
                            }
                        </div>
                        {song.padder &&
                            <div className="padder flex">
                                <button className="add">
                                    <MdAddCircle />
                                </button>
                                <button className="heart">
                                    <AiFillHeart />
                                </button>
                            </div>
                        }
                    </div>
                    <a href="/" className="img">
                        <img src={process.env.PUBLIC_URL + playLists.image} alt="" />
                    </a>
                </div>
                <div className="bottom">
                    <a href="/" className="name">{playLists.name}</a>
                    <a href="/" className="singer">{playLists.singer}</a>
                </div>
            </div >
        </div >
    );
}

export default Song;